import { ChangeDetectorRef, Component, ElementRef, HostListener, OnInit, ViewChild } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { BillingAPIService, InvoiceResponse } from 'src/services/BillingService';
import { MatTableDataSource } from '@angular/material/table';
import { PageInvoiceResponse } from 'src/services/BillingService/model/pageInvoiceResponse';
import { MatPaginator, PageEvent } from '@angular/material/paginator';
import { MatSnackBar, MatSnackBarConfig } from '@angular/material/snack-bar';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Router, ActivatedRoute } from '@angular/router';
import { HttpErrorResponse } from '@angular/common/http';

@Component({
  selector: 'app-list',
  templateUrl: './list.component.html',
  styleUrls: ['./list.component.css']
})
export class ListComponent implements OnInit {
  dataSource!: MatTableDataSource<InvoiceResponse>;
  col: string[] = ['invoiceId', 'customer', 'number', 'amount','description'];
  searching = false; // Loading indicator
  totalRecords = 0;
  pageSize = 10; // Page size
  pageIndex = 0; // Current page number
  // With the ! This is a way to tell TypeScript not to worry, that you are sure paginator will be initialized at some point.
  @ViewChild('paginator', { static: true }) paginator!: MatPaginator;
  appTitle = 'sotobotero'; // Variable that contains the application title
    
  // Initialize object with default values 
  entityForm: FormGroup;
  id: string = '';

  private readonly SNACKBAR_TYPES = {
    ERROR: 'error-snackbar',
    SUCCESS: 'success-snackbar',
    WARNING: 'warning-snackbar'
  } as const;


  private snackBarConfig: MatSnackBarConfig = {
    duration: 5000,
    horizontalPosition: 'center',
    verticalPosition: 'top', // Cambiado a top para que coincida con los estilos centrados
  };

 // @ViewChild(MatMenuTrigger) menuTrigger!: MatMenuTrigger;
  constructor(private serviceApi: BillingAPIService,
    private el: ElementRef,
    private router: Router,
    private route: ActivatedRoute,
    private cdr: ChangeDetectorRef,
    private snackBar: MatSnackBar,
    private fb: FormBuilder) {
      this.entityForm = this.fb.group({
        number: ['', Validators.required],
        customer: ['', [Validators.required, Validators.pattern('^[0-9]+$')]],   
        amount: [0, [Validators.required, Validators.pattern(/^\d+(\.\d{1,2})?$/)]],
        detail: ['']
    });
  }

  private showNotification(message: string, type: keyof typeof this.SNACKBAR_TYPES): void {
    const config: MatSnackBarConfig = {
      ...this.snackBarConfig,
      panelClass: [this.SNACKBAR_TYPES[type]]
    };

    this.snackBar.open(message, 'Cerrar', config);
  }

  ngOnInit(): void {
    this.getPagedData();
  }

  openwebsideHelp(): void {
    window.open('https://sotobotero.com', '_blank'); // Opens a new window or tab with the provided URL
  }

  getPagedData() {
    this.searching = true;

    this.serviceApi.getAllPaged(this.pageIndex, this.pageSize, 'body').subscribe({
      next: (results: PageInvoiceResponse) => {
        // Here you can handle the response from the service call
        this.totalRecords = results?.totalElements ?? 0;
        this.dataSource = new MatTableDataSource<InvoiceResponse>(results.content);
      },
      error: (err) => {
        if (err.status === 404) {
          this.dataSource = new MatTableDataSource<InvoiceResponse>([]); // Empty list
          console.warn('No records found.');
          this.showNotification('No se encontraron registros, empieza por ingresar los datos y luego pulsa el boton Add', 'WARNING');
        }
        this.handleError(err);
        this.cdr.detectChanges(); // Forces the change detection

      },
      complete: () => {
        this.searching = false;
        this.cdr.detectChanges(); // Forces the change detection
      }
    });

  }

  pageChangeEvent(event: PageEvent) {
    this.pageIndex = event.pageIndex;
    this.pageSize = event.pageSize;
    this.getPagedData();
  }

  create() {
    if (this.entityForm.valid) {
      console.info('Starting entity create' + JSON.stringify(this.entityForm.getRawValue()));
  
      // Logic to determine if it is POST or PUT
    const request$ = this.id === ''
      ? this.serviceApi.post(this.entityForm.value) // POST
      : this.serviceApi.put(this.entityForm.value, this.id); // PUT

      request$.subscribe({
        next: (response) => {
          console.log('Server response:', response);
  
          // After the request is successfully completed, update the list
          this.getPagedData(); // Update data without reloading the page
          this.entityForm.reset(); // Reset form
        },
        error: (err) => {
          console.error('Error sending request:', err);
          this.handleError(err);
        },
        complete: () => {
          console.info('Operation completed.');
        }
      });
    } else {
      alert('Please complete all required fields before saving.');
    }
  }


  private handleError(error: HttpErrorResponse): void {
    let errorMessage = 'Ha ocurrido un error en la aplicación';
    
    if (error.status === 0) {
      errorMessage = 'No hay conexión del front con el backend. Asegurate que no tienes restricciones de red en tu entorno de trabajo. '+ error.message;
    } else if (error.status === 404) {
      errorMessage = 'No se encontraron registros.';
    } else if (error.error instanceof ErrorEvent) {
      // Error del lado del cliente
      errorMessage = `Error: ${error.error.message}`;
    } else {
      // Error del lado del servidor
      errorMessage = `El servidor respondió con código ${error.status}. Mensaje: ${error.message}`;
    }
    this.showNotification(errorMessage, 'ERROR');
  }
}
